$blue: #2f86a6;
$green: #34be82;
$lgreen: #0fd17d;
$yellow: #f0ff24;
$lgrey: #f8f8f8;

.outer_box {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.bar_box {
  align-self: center;
  width: 630px;
  height: 300px;
  max-width: calc(100% - 30px);
  border-bottom: 5px solid $blue;
  border-left: 5px solid $blue;
  margin: 30px 5px 0 5px;
  padding: 0 25px;
  display: flex;
  justify-content: left;
  align-items: end;
  div {
    width: 60px;
    height: 250px;
    border-radius: 10px 10px 0 0;
    background-color: $lgreen;
    margin: 0 15px;
    position: relative;
    span {
      position: absolute;
      top: -30px;
      left: 0px;
      width: 60px;
      height: 30px;
      font-size: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  div:last-child {
    height: 40px;
  }
}
.date_box {
  align-self: center;
  width: 630px;
  max-width: calc(100% - 40px);
  height: 70px;
  margin: 0 10px;
  padding: 0 25px;
  border-bottom: 5px solid #fff;
  border-left: 5px solid #fff;
  display: flex;
  justify-content: left;
  align-items: center;
  div {
    width: 60px;
    height: 60px;
    border-radius: 200px;
    background-color: #e8e8e8;
    margin: 0 15px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    span {
      font-size: 20px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
.top_box {
  width: 630px;
  max-width: calc(100% - 20px);
  height: 40px;
  font-size: 18px;
  padding: 0;
  margin: 0 0;
  align-self: center;
  display: flex;
  justify-content: left;
  align-items: center;
  position: relative;
  span {
    margin: 0 10px;
    font-size: 18px;
  }
  input {
    width: 25px;
    height: 25px;
    margin: 0;
    &:hover {
      cursor: pointer;
    }
  }
}
.selectBox {
  width: 630px;
  max-width: calc(100% - 20px);
  height: 40px;
  padding: 0;
  margin: 10px 0;
  align-self: center;
  display: flex;
  justify-content: right;
  align-items: center;
  position: relative;
  select {
    border: 1px solid #cacaca;
    width: 360px;
    max-width: calc(100% - 20px);
    font-size: 20px;
    line-height: 30px;
    padding: 10px 10px 10px 15px;
    &:focus {
      outline: none;
    }
    &:hover {
      cursor: pointer;
    }
  }
}
